<template>
  <section class="commissions-tab">
    <v-dialog v-if="selectedPerson" v-model="dialog" min-width="300">
      <v-card>
        <v-card-title class="title">Detalles Comisiones</v-card-title>
        <v-card-text>{{selectedPerson.name.first}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn warning flat="flat" @click.native="dialog = false">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-layout class="pa-3">
      <v-flex>
        <disco-header :show-title="true"
                      title="Comisiones de los promotores"
        ></disco-header>

        <v-text-field v-model="search" placeholder="Buscar promotor por nombre..."></v-text-field>

        <v-list three-line v-if="promoters.length > 0">
          <template v-for="person in filteredPromoters">
            <v-list-tile avatar v-bind:key="person.title" @click="showDialog(person)">
              <v-list-tile-avatar>
                <img v-bind:src="person.picture.thumbnail">
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title><strong>{{person.name.last}} {{person.name.first}}</strong></v-list-tile-title>
                <v-list-tile-sub-title>comision: {{Math.floor((Math.random() * 1000000) + 1)}}Bs.</v-list-tile-sub-title>
                <v-list-tile-sub-title><small>invitados: {{Math.floor((Math.random() * 50) + 1)}} ● asistidos: {{Math.floor((Math.random() * 49) + 1)}}</small></v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </template>
          <h1>&nbsp;</h1>
          <h1>&nbsp;</h1>
        </v-list>
        <h5 v-else class="text-xs-center">¡No hay promotores para este evento!</h5>
      </v-flex>
    </v-layout>
  </section>
</template>

<script>
  import DiscoHeader from '../../partial/DiscoHeader'
  import { VAvatar, VTextField, VDialog } from 'vuetify'

  import {
    VCard,
    VCardTitle,
    VCardText,
    VCardActions
  } from 'vuetify/es5/components/VCard'

  export default {
    mounted () {
      this.getPromoters()
    },
    data () {
      return {
        fab: false,
        dialog: false,
        search: '',
        selectedPerson: null,
        promoters: []
      }
    },
    computed: {
      filteredPromoters () {
        if (this.search !== '') {
          return this.promoters.filter(
            person => {
              return (person.name.first + person.name.last).toLowerCase().indexOf(this.search.toLowerCase()) > 0
            }
          )
        } else {
          return this.promoters
        }
      }
    },
    methods: {
      getPromoters () {
        let params = {
          params: {results: Math.floor((Math.random() * 30) + 1), seed: 'promoters', nat: 'es', exc: 'login, id, location'}
        }
        this.$http.get('https://randomuser.me/api', params).then(
          response => {
            if (response.ok) this.promoters = response.data.results
            console.log(response)
          }
        ).catch(
          error => {
            console.log(error)
          }
        )
      },
      showDialog (promoter) {
        this.selectedPerson = promoter
        this.dialog = true
      }
    },
    components: {
      DiscoHeader,
      VTextField,
      VAvatar,
      VDialog,
      VCard,
      VCardTitle,
      VCardText,
      VCardActions
    }
  }
</script>

<style lang="stylus" scoped>
  .list
    background-color: #fafafa
</style>



