import Vue from 'vue'
import Vuex from 'vuex'
import VueResource from 'vue-resource'
import VuejsStorage from 'vuejs-storage'

Vue.use(Vuex)
Vue.use(VueResource)
Vue.use(VuejsStorage)

export const store = new Vuex.Store({
  state: {
    user: {
      full_name: 'Edgar Messia',
      email: 'edgar.m1986@gmail.com',
      photo: 'https://i.ytimg.com/vi/jTL_sJycQAA/maxresdefault.jpg',
      role: 'manager',
      promoter: 'Daniel Perez',
      config: {
        notification_promoter: true,
        notification_manager: true,
        notification_events: true,
        notification_promotions: true
      }
    },
    disco: {
      name: 'G Disco & Drinks',
      logo: 'https://graffiatobymic.com/assets/sites/7/2016/12/G-icon-rotated10-bw.png'
    }
  },
  mutations: {
    login (state, user) {
      state.user = user
    },
    logout (state) {
      state.user = {}
    },
    role (state, role) {
      state.user.role = role
    }
  },
  getters: {
    logued (state) {
      return state.user.api_token && state.user.api_token.length > 0
    },
    userIsManager (state) {
      return state.user.role === 'manager'
    },
    userIsPromoter (state) {
      return state.user.role === 'promoter'
    },
    userIsReferred (state) {
      return state.user.role === 'referred'
    }
  },
  actions: {
    login (context, request) {
      Vue.http.post('/login', {email: request.email, password: request.password})
        .then(
          (response) => {
            return new Promise((resolve, reject) => {
              if (response.ok && response.data.api_token) {
                context.commit('login', response.data)
                resolve(response.data)
              } else {
                context.commit('logout')
                // reject()
              }
            })
          }
        )
    }
  },
  plugins: [
    VuejsStorage({
      namespace: 'eventos',
      storage: window.localStorage
    })
  ]
})



