<template>
  <section class="assistants-tab">
    <v-dialog v-if="selectedPerson" v-model="dialog" min-width="300">
      <v-card>
        <v-card-title class="title">Detalles de asistencia</v-card-title>
        <v-card-text>
          <h5>Nombre completo: {{selectedPerson.name.last + ' ' +selectedPerson.name.first}}</h5>
          <h5>Invitado por: xxxxx</h5>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn warning flat @click.native="dialog = false">Cancelar</v-btn>
          <v-speed-dial
            direction="top"
            transition="slide-y-reverse-transition"
          >
            <v-btn
              v-show="!selectedPerson.status"
              primary
              slot="activator"
              class="elevation-16"
            >
              Marcar
            </v-btn>
            <v-btn
              dark
              small
              color="green"
              @click="confirmAssistance('e')"
            >
              entrada
            </v-btn>
            <v-btn
              dark
              small
              color="blue"
              @click="confirmAssistance('c')"
            >
              consumo
            </v-btn>
          </v-speed-dial>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-layout class="pa-3">
      <v-flex>
        <disco-header :show-title="false">
          <h4 class="text-xs-center">Asistentes de hoy ({{assistants.length}})</h4>
        </disco-header>

        <v-text-field v-model="search" placeholder="Buscar asistente por nombre..."></v-text-field>

        <v-list two-line v-if="assistants.length > 0">
          <template v-for="person in filteredAssistants">
            <v-list-tile avatar v-bind:key="person.title" @click="showDialog(person)">
              <v-list-tile-avatar>
                <img v-bind:src="person.picture.thumbnail">
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title><strong>{{person.name.last}} {{person.name.first}}</strong></v-list-tile-title>
                <v-list-tile-sub-title>
                  <strong v-if="person.status === 'e'">asistio ● <span class="text--green">entrada</span></strong>
                  <strong v-else-if="person.status === 'c'">asistio ● <span class="text--indigo">consumo</span></strong>
                  <span v-else>esperando...</span>
                </v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-avatar>
                <v-btn fab
                       :info="person.status === 'c'"
                       :success="person.status === 'e'"
                       :warning="!person.status"
                       small
                       class="elevation-0"
                >
                  <v-icon>{{(person.status) ? 'thumb_up': 'thumb_down'}}</v-icon>
                </v-btn>
              </v-list-tile-avatar>
            </v-list-tile>
          </template>
          <h1>&nbsp;</h1>
          <h1>&nbsp;</h1>
        </v-list>
        <h5 v-else class="text-xs-center">¡No hay invitados para este evento!</h5>
      </v-flex>
    </v-layout>

    <v-speed-dial
      v-model="fab"
      fixed
      right
      direction="top"
      transition="slide-y-reverse-transition"
    >
      <v-btn
        v-model="fab"
        slot="activator"
        primary
        dark
        fab
        class="elevation-16"
      >
        <v-icon>person_add</v-icon>
        <v-icon>close</v-icon>
      </v-btn>
      <v-btn
        dark
        small
        color="green"
      >
        entrada
      </v-btn>
      <v-btn
        dark
        small
        color="blue"
      >
        consumo
      </v-btn>
    </v-speed-dial>
  </section>
</template>

<script>
  import DiscoHeader from '../../partial/DiscoHeader'
  import { VSpeedDial, VAvatar, VTextField, VDialog } from 'vuetify'

  import {
    VCard,
    VCardTitle,
    VCardText,
    VCardActions
  } from 'vuetify/es5/components/VCard'

  export default {
    mounted () {
      this.getAssistants()
    },
    data () {
      return {
        fab: false,
        dialog: false,
        search: '',
        selectedPerson: null,
        assistants: []
      }
    },
    computed: {
      filteredAssistants () {
        if (this.search !== '') {
          return this.assistants.filter(
            person => {
              return (person.name.first + person.name.last).toLowerCase().indexOf(this.search.toLowerCase()) > 0
            }
          )
        } else {
          return this.assistants
        }
      }
    },
    methods: {
      getAssistants () {
        let params = {
          params: {results: Math.floor((Math.random() * 30) + 1), seed: 'assistants', nat: 'es', exc: 'login, id, location'}
        }
        this.$http.get('https://randomuser.me/api', params).then(
          response => {
            if (response.ok) this.assistants = response.data.results
            console.log(response)
          }
        ).catch(
          error => {
            console.log(error)
          }
        )
      },
      showDialog (assistant) {
        this.selectedPerson = assistant
        this.dialog = true
      },
      confirmAssistance (status) {
        this.selectedPerson.status = status
        this.dialog = false
      }
    },
    components: {
      DiscoHeader,
      VSpeedDial,
      VTextField,
      VAvatar,
      VDialog,
      VCard,
      VCardTitle,
      VCardText,
      VCardActions
    }
  }
</script>

<style lang="stylus" scoped>
  .speed-dial--right
    bottom: 74px !important
  .list
    background-color: #fafafa
</style>



