<template>
  <section class="my-invitations-tab">
    <v-layout class="pa-3">
      <v-flex>
        <disco-header :show-title="false">
          <h4 class="text-xs-center">Invitados de hoy ({{invitations.length}})</h4>
        </disco-header>

        <v-text-field v-model="search" placeholder="Buscar invitado por nombre..."></v-text-field>

        <v-list two-line v-if="invitations.length > 0">
          <template v-for="person in filteredInvitations">
            <v-list-tile avatar v-bind:key="person.title">
              <v-list-tile-avatar>
                <img v-bind:src="person.picture.thumbnail">
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title><strong>{{person.name.last}} {{person.name.first}}</strong></v-list-tile-title>
                <v-list-tile-sub-title>
                  <strong v-if="person.status === 'e'">asistio ● <span class="text--green">entrada</span></strong>
                  <strong v-else-if="person.status === 'c'">asistio ● <span class="text--indigo">consumo</span></strong>
                  <span v-else>esperando...</span>
                </v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-avatar>
                <v-btn fab
                       :info="person.status === 'c'"
                       :success="person.status === 'e'"
                       :warning="!person.status"
                       small
                       class="elevation-0"
                >
                  <v-icon>{{(person.status) ? 'thumb_up': 'thumb_down'}}</v-icon>
                </v-btn>
              </v-list-tile-avatar>
            </v-list-tile>
          </template>
          <h1>&nbsp;</h1>
          <h1>&nbsp;</h1>
        </v-list>
        <h5 v-else class="text-xs-center">¡No hay invitados para este evento!</h5>
      </v-flex>
    </v-layout>

    <v-speed-dial
      v-model="fab"
      fixed
      right
      direction="top"
      transition="slide-y-reverse-transition"
    >
      <v-btn
        v-model="fab"
        slot="activator"
        primary
        dark
        fab
        class="elevation-16"
      >
        <v-icon>person_add</v-icon>
        <v-icon>close</v-icon>
      </v-btn>
      <v-btn
        dark
        small
        color="green"
      >
        sms
      </v-btn>
      <v-btn
        dark
        small
        color="blue"
      >
        email
      </v-btn>
      <v-btn
        dark
        small
        color="black"
      >
        codigo qr
      </v-btn>
      <v-btn
        dark
        small
        color="red"
      >
        url
      </v-btn>
    </v-speed-dial>
  </section>
</template>

<script>
  import DiscoHeader from '../../partial/DiscoHeader'
  import { VSpeedDial, VAvatar, VTextField } from 'vuetify'

  export default {
    mounted () {
      this.getInvitations()
    },
    data () {
      return {
        fab: false,
        search: '',
        selectedPerson: null,
        invitations: []
      }
    },
    computed: {
      filteredInvitations () {
        if (this.search !== '') {
          return this.invitations.filter(
            person => {
              return (person.name.first + person.name.last).toLowerCase().indexOf(this.search.toLowerCase()) > 0
            }
          )
        } else {
          return this.invitations
        }
      }
    },
    methods: {
      getInvitations () {
        let params = {
          params: {results: Math.floor((Math.random() * 30) + 1), seed: 'invitations', nat: 'es', exc: 'login, id, location'}
        }
        this.$http.get('https://randomuser.me/api', params).then(
          response => {
            if (response.ok) this.invitations = response.data.results
            console.log(response)
          }
        ).catch(
          error => {
            console.log(error)
          }
        )
      }
    },
    components: {
      DiscoHeader,
      VSpeedDial,
      VTextField,
      VAvatar
    }
  }
</script>

<style lang="stylus" scoped>
  .speed-dial--right
    bottom: 74px !important
  .list
    background-color: #fafafa
</style>



