<template>
  <section class="config-page">
    <v-layout wrap row justify-space-between class="pa-3">
      <v-flex xs12>
        <h4 class="text-xs-center">Configurar Notificaciones</h4>
        <h4 class="text-xs-center">
          <small>aquí podrás configurar las notificaciones que quieres recibir en el app</small>
        </h4>
      </v-flex>
      <v-flex>
        <v-switch label="Notificaciones del promotor" v-model="config.notification_promoter"></v-switch>
        <v-switch label="Notificaciones del gerente" v-model="config.notification_manager"></v-switch>
        <v-switch label="Notificaciones de nuevos eventos" v-model="config.notification_events"></v-switch>
        <v-switch label="Notificaciones de promociones" v-model="config.notification_promotions"></v-switch>
      </v-flex>
      <v-btn warning fixed bottom right :to="{name: 'Main'}">Volver</v-btn>
    </v-layout>
  </section>
</template>

<script>
  import {VSwitch} from 'vuetify'

  export default {
    data () {
      return {}
    },
    computed: {
      config () {
        return this.$store.state.user.config
      }
    },
    components: {
      VSwitch
    }
  }
</script>



