<template>
  <section class="register-page">
    <v-layout wrap class="pa-3">
      <v-flex xs12>
        <h1 class="text-xs-center">Logo</h1>
      </v-flex>
      <v-flex xs12>
        <h4 class="text-xs-center">Registro de nuevo referido</h4>
        <v-stepper v-model="step">
          <v-stepper-header>
            <v-stepper-step step="1"
                            :complete="stepOneValid"
                            :rules="stepOneRules">
              Correo electrónico
              <small v-show="!stepOneValid">correo electrónico invalido</small>
            </v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="2"
                            :complete="(full_name && full_name.length > 0 && phone && phone.length > 0)"
                            :rules="stepTwoRules"
            >
              Datos Personales
              <small v-show="!stepTwoValid">nombre o teléfono celular invalido</small>
            </v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="3"
                            :complete="(password && password.length > 0 && password_confirmation && password_confirmation.length > 0)"
                            :rules="stepFourRules"
            >
              Seguridad
              <small v-show="!stepFourValid">contraseña invalida</small>
            </v-stepper-step>
          </v-stepper-header>
          <v-stepper-items>
            <v-stepper-content step="1">
              <v-text-field
                label="Correo Electrónico"
                min="10"
                max="100"
                v-model="email"
                type="email"
                counter
              ></v-text-field>
              <v-text-field
                label="Repite tu Correo Electrónico"
                min="10"
                max="100"
                v-model="email_confirmation"
                type="email"
                counter
              ></v-text-field>
              <p>el correo electrónico no sera visible para nadie, solo con fines de seguridad.</p>
              <v-btn primary @click.native="step++">
                <v-icon>navigate_next</v-icon>
              </v-btn>
            </v-stepper-content>
            <v-stepper-content step="2">
              <v-text-field
                label="Nombre completo"
                min="10"
                max="30"
                v-model="full_name"
                counter
              ></v-text-field>
              <v-text-field
                label="Numero de Teléfono"
                min="10"
                max="30"
                v-model="phone"
                type="phone"
                counter
              ></v-text-field>
              <p>tu numero de teléfono solo sera usado por tu anfitrión.</p>
              <v-btn flat @click.native="--step">
                <v-icon>navigate_before</v-icon>
              </v-btn>
              <v-btn primary @click.native="step++">
                <v-icon>navigate_next</v-icon>
              </v-btn>
            </v-stepper-content>
            <v-stepper-content step="3">
              <v-text-field
                label="Introduce tu contraseña"
                hint="Al menos 8 caracteres"
                v-model="password"
                min="8"
                :append-icon="e1 ? 'visibility' : 'visibility_off'"
                :append-icon-cb="() => (e1 = !e1)"
                :type="e1 ? 'password' : 'text'"
                counter
              ></v-text-field>
              <v-text-field
                label="Repite tu contraseña"
                hint="Al menos 8 caracteres"
                v-model="password_confirmation"
                min="8"
                :append-icon="e2 ? 'visibility' : 'visibility_off'"
                :append-icon-cb="() => (e2 = !e2)"
                :type="e2 ? 'password' : 'text'"
                counter
              ></v-text-field>
              <v-btn flat @click.native="--step">
                <v-icon>navigate_before</v-icon>
              </v-btn>
              <v-btn info :to="{'name': 'Main'}" :disabled="!formValid || loading" :loading="loading">
                <v-icon>check</v-icon>
              </v-btn>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
        <v-btn block warning :to="{name: 'Login'}">Cancelar</v-btn>
      </v-flex>
    </v-layout>
  </section>
</template>

<script>
  import { VDivider, VTextField } from 'vuetify'

  import {
    VStepper,
    VStepperHeader,
    VStepperStep,
    VStepperItems,
    VStepperContent
  } from 'vuetify/es5/components/VStepper'

  export default {
    data () {
      return {
        step: null,
        full_name: null,
        email: null,
        phone: null,
        password: null,
        email_confirmation: null,
        password_confirmation: null,
        photo: null,
        e1: false,
        e2: false,
        loading: false
      }
    },
    computed: {
      stepOneValid () {
        return this.email && this.email.length > 5 && this.email_confirmation && this.email_confirmation.length > 5 && this.email === this.email_confirmation
      },
      stepTwoValid () {
        return this.full_name && this.full_name.length > 3 && this.phone && this.phone.length > 7
      },
      stepTreeValid () {
        return true
      },
      stepFourValid () {
        return this.password && this.password.length > 7 && this.password_confirmation && this.password_confirmation.length > 7 && this.password === this.password_confirmation
      },
      formValid () {
        return (this.stepOneValid && this.stepTwoValid && this.stepFourValid)
      },
      stepOneRules () {
        return [() => (this.stepOneValid)]
      },
      stepTwoRules () {
        return [
          () => (this.stepTwoValid)
        ]
      },
      stepFourRules () {
        return [
          () => (this.stepFourValid)
        ]
      }
    },
    components: {
      VStepper,
      VStepperHeader,
      VStepperStep,
      VStepperItems,
      VStepperContent,
      VDivider,
      VTextField
    }
  }
</script>



