<template>
  <section class="profile-page text-xs-center">
    <v-layout wrap class="pa-0">
      <v-flex xs12>
        <h4>Mi Perfil</h4>
        <div :style="{'background-image': 'url(' + user.photo + ')' }" class="portrait">
          <v-layout column align-center justify-center>
            <h1><v-icon dark>perm_identity</v-icon> {{user.full_name}}</h1>
            <h4><v-icon dark>email</v-icon> {{user.email}}</h4>
          </v-layout>
        </div>
        <v-btn small>Editar Perfil</v-btn>
      </v-flex>
      <v-flex xs12 v-show="$store.getters.userIsReferred">
        <h4><strong>Mi promotor:</strong> {{user.promoter}}</h4>
      </v-flex>
      <v-btn warning fixed bottom right :to="{name: 'Main'}">Volver</v-btn>
    </v-layout>
  </section>
</template>

<script>
  export default {
    data () {
      return {

      }
    },
    computed: {
      user () {
        return this.$store.state.user
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .portrait
    min-height: 300px
    background-repeat no-repeat
    background-position top center
    background-size: 100%
    color: white
    text-shadow: 1px 1px 1px black
</style>



