<template>
  <header class="text-xs-center">
    <div v-show="showDiscoLogo">
      <v-avatar
        :size="logoSize"
        class="grey lighten-4"
      >
        <img :src="disco.logo" alt="Logo">
      </v-avatar>
    </div>
    <h3 v-show="showDiscoName">{{disco.name}}</h3>
    <h4 v-show="showTitle" v-html="title"></h4>
    <slot></slot>
    <h6 v-show="showDate">{{new Date() | moment("DD/MM/YYYY")}}</h6>
  </header>
</template>

<script>
  import {VAvatar} from 'vuetify'

  export default {
    props: {
      logoSize: {
        type: String,
        default: '80px'
      },
      title: {
        type: String,
        default: null
      },
      showDiscoLogo: {
        type: Boolean,
        default: true
      },
      showDiscoName: {
        type: Boolean,
        default: true
      },
      showTitle: {
        type: Boolean,
        default: false
      },
      showDate: {
        type: Boolean,
        default: true
      }
    },
    computed: {
      disco () {
        return this.$store.state.disco
      }
    },
    components: {
      VAvatar
    }
  }
</script>



