<template>
  <section class="login-page">
    <v-layout wrap class="pa-3">
      <v-flex xs12>
        <h1 class="text-xs-center">Logo</h1>
      </v-flex>
      <v-flex xs12>
        <v-text-field
          name="email"
          label="Correo Electrónico"
          v-model="email"
          type="email"
          prepend-icon="email"
          single-line
        ></v-text-field>
        <v-text-field
          name="password"
          label="Contraseña"
          hint="Al menos 8 caracteres"
          v-model="password"
          min="8"
          :append-icon="txtVisible ? 'visibility' : 'visibility_off'"
          :append-icon-cb="() => (txtVisible = !txtVisible)"
          :type="txtVisible ? 'password' : 'text'"
          prepend-icon="vpn_key"
          counter
        ></v-text-field>
        <div class="text-xs-right">
          <v-btn :to="{name: 'Register'}">Registro</v-btn>
          <v-btn color="primary" :to="{name: 'Main'}">Entrar</v-btn>
        </div>
      </v-flex>
    </v-layout>
  </section>
</template>

<script>
  import {VTextField} from 'vuetify'

  export default {
    data () {
      return {
        txtVisible: false,
        email: '',
        password: ''
      }
    },
    components: {
      VTextField
    }
  }
</script>

<style lang="stylus" scoped>

</style>



