<template>
  <section class="main-page">
    <v-toolbar flat class="grey lighten-3">
      <v-toolbar-title>logo promotora</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-menu bottom left>
        <v-btn icon slot="activator">
          <v-icon>more_vert</v-icon>
        </v-btn>
        <v-list subheader>
          <v-list-tile :to="{name: 'Profile'}">
            <v-list-tile-title>Mi Perfil</v-list-tile-title>
          </v-list-tile>
          <v-list-tile :to="{name: 'Config'}">
            <v-list-tile-title>Configurar Notificaciones</v-list-tile-title>
          </v-list-tile>
          <v-list-tile :to="{name: 'Login'}">
            <v-list-tile-title>Salir</v-list-tile-title>
          </v-list-tile>

          <!--Borrar cuando este la app final-->
          <v-divider></v-divider>
          <v-subheader>Solo para pruebas del app</v-subheader>
          <v-radio-group v-model="selectedRole" :mandatory="false">
            <v-list-tile avatar>
              <v-list-tile-action>
                <v-radio value="manager"></v-radio>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>Gerente</v-list-tile-title>
                <v-list-tile-sub-title>role gerente</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-action>
                <v-radio value="promoter"></v-radio>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>Promotor</v-list-tile-title>
                <v-list-tile-sub-title>role promotor</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-action>
                <v-radio value="referred"></v-radio>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>Referido</v-list-tile-title>
                <v-list-tile-sub-title>role referido</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-radio-group>
        </v-list>
      </v-menu>
    </v-toolbar>
    <v-tabs fixed centered icons grow v-model="activeTab">
      <v-tabs-items>
        <v-tabs-content
          v-for="tab in roles[user.role].tabs"
          :key="tab.label"
          :id="'tab-' + tab.label"
        >
          <component :is="tab.component"></component>
        </v-tabs-content>
      </v-tabs-items>
      <v-tabs-bar class="grey lighten-3">
        <v-tabs-slider color="primary"></v-tabs-slider>
        <v-tabs-item
          v-for="tab in roles[user.role].tabs"
          :key="tab.label"
          :href="'#tab-' + tab.label"
        >
          <v-icon>{{tab.icon}}</v-icon>
          {{ tab.label }}
        </v-tabs-item>
      </v-tabs-bar>
    </v-tabs>
  </section>
</template>

<script>
  // Manager Tabs
  import AssistantsTab from '../tabs/manager/Assistants'
  import CommissionsTab from '../tabs/manager/Commissions'
  // Promoter Tabs
  import MyCommissions from '../tabs/promoter/MyCommissions'
  import MyInvitations from '../tabs/promoter/MyInvitations'

  import {
    VTabs,
    VTabsBar,
    VTabsSlider,
    VTabsItems,
    VTabsItem,
    VTabsContent
  } from 'vuetify/es5/components/VTabs'

  import {
    VCard,
    VCardText
  } from 'vuetify/es5/components/VCard'

  // Para borrar sol con proposito de probar las diferentes interfaces
  import { VRadioGroup, VRadio } from 'vuetify/es5/components/VRadioGroup'

  import { VMenu, VDivider, VSubheader } from 'vuetify'

  export default {
    data () {
      return {
        activeTab: null,
        roles: {
          manager: {
            tabs: [
              {label: 'Asistencias', icon: 'people', component: AssistantsTab},
              {label: 'Comisiones', icon: 'attach_money', component: CommissionsTab}
            ]
          },
          promoter: {
            tabs: [
              {label: 'Invitados', icon: 'group_add', component: MyInvitations},
              {label: 'Comisiones', icon: 'attach_money', component: MyCommissions}
              // {label: 'Información', icon: 'info', component: null}
            ]
          },
          referred: {
            tabs: [
              {label: 'Información', icon: 'info', component: null}
            ]
          }
        }
      }
    },
    computed: {
      user () {
        return this.$store.state.user
      },
      selectedRole: {
        get () {
          return this.$store.state.user.role
        },
        set (role) {
          this.$store.commit('role', role)
          window.location.reload()
        }
      }
    },
    components: {
      VTabs,
      VTabsBar,
      VTabsSlider,
      VTabsItems,
      VTabsItem,
      VTabsContent,
      VCard,
      VCardText,
      VMenu,
      VDivider,
      VRadioGroup,
      VRadio,
      VSubheader
    }
  }
</script>

<style lang="stylus" scoped>
  .tabs__bar
    position: fixed
    bottom: 0
</style>



